<template>
  <div id="app">
    <ul style="float:left" v-for="item in programs">
      <h2>{{item.computerName}}</h2>
      <li v-for="content in item.content">{{content}}</li>
    </ul>
    <OneComputer />
  </div>
</template>

<script>
import OneComputer from "./components/OneComputer.vue";

export default {
  name: "app",
  data() {
    return {
      programs: []
    };
  },
  components: {
    OneComputer
  },
  methods: {
    /*

burada bağlantısı kopan socket siliniyor
    */
    deleteSocket: function(socket) {
      for (var i = 0; i < this.programs.length; i++) {
        if (this.programs[i].id === socket) {
          this.programs.splice(i, 1);
          console.log("silindi");
          return;
        }
      }
    },
    /*
bu alttakiyle asıl işlem yapılıyot tuşa basıldığında servden buraya 
event gelip event sonucu yazının basılması işlemi yapılıyor
    */
    addEvent: function(event) {
      console.log(event);
      for (var i = 0; i < this.programs.length; i++) {
        if (this.programs[i].id === event.id) {
          this.programs[i].content.push(event.text);

          return;
        }
      }
    }
  },
  sockets: {
    disconnect: function() {
      this.programs = [];
    },
    /*
bağlantı yapıldığında bunun view olduğu servera gönderriliyor
*/
    connect: function() {
      console.log("connected");
      var data = {
        client: "view"
      };

      this.$socket.emit("connected", data);
    },
    /*
eğer servera bir progtam bağlanmışsa buarada çıkmakta

    */
    programAdded: function(data) {
      console.log(data);
      var programData = {
        id: data.id,
        computerName: data.computerName,
        content: []
      };
      this.programs.push(programData);
    },
    programDeleted: function(socketId) {
      console.log(socketId + "silindi");
      this.deleteSocket(socketId);
    },
    /*
asıl bizim mevzumuz burası burada 
tuşsa basıldığında serverdan gönderilen veri alınmakta
    */
    changed: function(event) {
      this.addEvent(event);
    }
  }
};
</script>

