import Vue from 'vue'
import VueSocketIO from 'vue-socket.io'
import App from './App.vue'

var isDevelopment = false;
var path = "";
if (isDevelopment) {
  path = "localhost";
} else {
  path = 'https://intense-cove-31943.herokuapp.com'
}

Vue.use(new VueSocketIO({
  debug: false,
  connection: path,
  options: {
    path: "/socket.io"
  } //Optional options

}))

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
}).$mount('#app')